import Image from 'next/image';

export default function GameStore() {
  return (
    <div className="bg-gray-900 text-white min-h-screen p-6">
      <div className="flex flex-col md:flex-row gap-6">
        {/* Sidebar */}
        <div className="w-full md:w-1/4 bg-gray-800 p-4 rounded-lg">
          <h2 className="text-lg font-semibold mb-4">COSMOX</h2>
          <nav className="space-y-2">
            <a href="#" className="block p-2 bg-gray-700 rounded hover:bg-gray-600">Tienda de Juegos</a>
            <a href="#" className="block p-2 bg-gray-700 rounded hover:bg-gray-600">Categorías</a>
            <a href="#" className="block p-2 bg-gray-700 rounded hover:bg-gray-600">Configuración</a>
            <a href="#" className="block p-2 bg-gray-700 rounded hover:bg-gray-600">Ayuda</a>
          </nav>
        </div>

        {/* Main Content */}
        <div className="flex-1">
          <div className="flex justify-between items-center mb-6">
            <div className="text-lg font-semibold">Descubrir</div>
            <div className="text-lg font-semibold">Noticias</div>
          </div>

          {/* Highlight Section */}
          <div className="bg-gray-800 p-4 rounded-lg mb-6">
            <div className="flex flex-col md:flex-row gap-4">
              <div className="flex-1">
                <Image src="/league-of-legends.jpg" alt="League of Legends" width={500} height={300} className="rounded-lg" />
                <button className="mt-4 bg-blue-500 px-4 py-2 rounded hover:bg-blue-400">Descargar</button>
              </div>
              <div className="flex-1 text-left">
                <h2 className="text-xl font-semibold mb-2">League of Legends</h2>
                <p className="text-sm mb-4">Detalles del juego, descripción breve y características principales del juego.</p>
                <span className="text-yellow-400 text-lg">$44.99</span>
              </div>
            </div>
          </div>

          {/* Downloaded Games Section */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            {["God of War", "Cyberpunk 2077", "Hogwarts Legacy", "Control"].map((game, index) => (
              <div key={index} className="bg-gray-800 p-4 rounded-lg">
                <Image src={`/${game.toLowerCase().replace(/ /g, '-')}.jpg`} alt={game} width={150} height={100} className="rounded-lg" />
                <h3 className="mt-2 text-lg font-semibold">{game}</h3>
                <span className="text-sm text-yellow-400">$9.99</span>
              </div>
            ))}
          </div>
        </div>

        {/* Right Sidebar */}
        <div className="w-full md:w-1/4 bg-gray-800 p-4 rounded-lg">
          <h2 className="text-lg font-semibold mb-4">Invitado</h2>
          <button className="bg-blue-500 px-4 py-2 rounded hover:bg-blue-400 w-full mb-4">Login</button>
          <div className="space-y-2">
            {["League of Legends", "God of War", "Cyberpunk 2077", "Control", "Hogwarts Legacy"].map((game, index) => (
              <div key={index} className="bg-gray-700 p-2 rounded hover:bg-gray-600">
                {game}
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}
